.prev_page_btn
{
    display: flex;
    align-items: center;
    color: #000;
    justify-content: center;
    box-shadow: 0 0 10px 5px rgba(189, 171, 171, 0.5);
    padding: 8px 0;
    width: 150px;
    margin: 20px;
    border-radius: 3px;
}

.prev_page_btn svg
{
    margin-right: 10px;
}


.overwiev
{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
}

.pr_img , .pr_info
{
    width: 50%;
    height: 70vh;
    padding: 20px;
}


.pr_img
{
    overflow: hidden;
}


.pr_img img
{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.colors_pr
{
    display: flex;
}

.colors_pr span
{
    width: 30px;
    height: 30px;
    margin: 10px 10px 10px 0;
    border-radius: 50%;
    transition: .5s;
    opacity: .2;
}

.colors_pr span.active
{
    opacity: 1;
}

.pr_info
{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}



.buy_btn
{
    background: rgb(0, 129, 250);
    color: #fff;
    width: 100px;
    padding: 5px 20px;
    text-align: center;
    border-radius: 50px;
    transition: .5s;
}

.buy_btn:hover
{   
    opacity: .7;    
}


@media (max-width: 500px) {
    .overwiev
    {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }

    .pr_img , .pr_info
    {
        width: 100%;
        height: auto;
        padding: 20px;
    }
    
    .pr_info h1 ,h3, h4, p, div
    {
        margin-bottom: 10px;
    }
}