.products
{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    padding: 50px;
    width: 100%;
    height: auto;
}

.box
{
    box-shadow: 0 0 10px 10px rgba(218, 218, 218, 0.5);
    height: auto;
    padding: 50px;
    text-align: center;
    border-radius: 10px;
}

.img-pr
{
    width: 100%;
    height: 90%;
    border-radius: 10px;
    overflow: hidden;
}

.img-pr img
{
    width: 100%;
    height: 100%;
    object-fit: contain;
}


.more_btn
{
    padding: 5px 20px;
    border-radius: 50px;
    color: #fff;
    background: rgb(0, 162, 255);
    margin-top: 40px !important;
}

.name-pr
{
    margin-bottom: 10px;
}





@media (max-width: 500px) {
    .products
    {
        display: grid;
        grid-template-columns: repeat(1,1fr);
        gap: 10px;
        padding: 10px;
        width: 100%;
        height: auto;
    }
}

